@import url(https://fonts.googleapis.com/css2?family=Bungee&family=Bungee+Inline&family=Work+Sans&display=swap);
:root {
  --mini-margin: 8px;
  --small-margin:  calc(var(--mini-margin) * 2);
  --big-margin: calc(var(--mini-margin) * 3);
  --dark-blue: #1257BF;
  --mid-blue: #5392CC;
  --dark-red: #D85858;
  --mid-red: #E08989;
  --bg: #FAF2E6;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FAF2E6;
  background-color: var(--bg);
  font-size: min(calc(3px + 0.85vw), 16px);
  overflow-y: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sr-only {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important; 
  -webkit-clip-path: inset(50%) !important;
    clip-path: inset(50%) !important;  
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}


@media screen and (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }
}

.step {
	position: relative;
	counter-increment: big-step 1;
}

.step > div {
	margin-bottom: calc(var(--big-margin) * 3);
}

.step h3 {
	display: flex;
	position: relative;
	color: var(--dark-blue);
	height: 100%;
	align-items: center;
}

.step h3:before {
    content: counter(big-step);
    font-family: "Bungee Inline", sans-serif;
    font-weight: normal;
    font-size: 2.4em;
    display: flex;
    align-items: center;
    flex: 0 0 0.75em;
}

.step p {
	margin:  0;
	margin-bottom: 1em;
	color: var(--dark-blue);
}

.step:nth-child(even) p {
	color: var(--mid-blue);
}


.step:nth-child(even) h3{
	color: var(--mid-blue);
}

@media all and (min-width: 1200px) {
	.step h3 {
	}

	.step h3:before {
	    font-size: 0.7em;
	    height: 1.5em;
	    flex: 0 0 2em;
	    background-image: url(/holo-assistant/static/media/dark-heart.940d9d48.svg);
	    background-size: contain;
  		background-repeat: no-repeat;
  		background-position: 50% 54%;
  		margin-left: -1em;
  		display: grid;
  		text-align: center;
  		color: var(--bg);
	}

	.step:nth-child(even) h3:before {
  		background-image: url(/holo-assistant/static/media/mid-heart.16a2ae42.svg);
	}
	

}

.contentWrapper {
  margin:  0 auto;
  max-width: 1350px;
}

.content {
  margin: var(--big-margin);
}

.content > * > *:not(.sr-only):not(.heroVideo) {
  margin-top:  var(--small-margin);
  margin-bottom: 0px;
}

.fullWidth {
  width:  100%;
}

main {
  width:  80%;
  margin: 7em auto;  
  position: relative;
  counter-reset: big-step 0;
}

h1, h2, h3 {
  font-family: 'Bungee';
  font-weight: 500;
}

h2 {
  font-size: 4.25em;
  line-height: 95%;
  color: var(--mid-blue);
}

h3 {
  font-size: 4em;
  line-height: 98%;
}

p {
  font-size: 2em;
  font-family: 'Work Sans';
}

.heroVideo {
  aspect-ratio: 16 / 9;
  width: 100%;
  background: linear-gradient(180deg, #9CC5EB 0%, rgba(156, 197, 235, 0) 100%);
  position: relative;
  margin:  2em 0;
}

.navList {
  font-family: 'Bungee';
  font-size: 3em;
  line-height: 95%;
  counter-reset: small-step 0;
  position: relative;
}

.navList span {
  counter-increment: small-step 1;
}

.navList span a {
  position: relative;
  z-index: 3;
  margin-right: var(--small-margin);
}

.navList span a:hover {
  z-index: 1;
}


.navList span, .navList span a {
  color: var(--dark-blue);
  text-decoration: none;
}

.navList span:before {
  content: counter(small-step);
  font-size: 0.45em;
  display: inline-block;
  vertical-align: bottom;
  width: 1.9em;
  text-align: center;
  background-image: url(/holo-assistant/static/media/dark-heart.940d9d48.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 54%;
  margin: 0 var(--small-margin) 0 0;
  color: var(--bg);
  background-color: var(--bg);
  z-index: 3;
}


.navList span:after {
  background-image: url(/holo-assistant/static/media/sparkle.494d81cc.svg);
  background-size: cover;
  content:  '';
  position: absolute;
  top:  0px;
  left:  0px;
  width: 100%;
  height: 100%;
  z-index: 2;
  pointer-events: none;
  opacity: 0;
}

.navList span:hover:after {
  left: 2%;
  top:  -5%;
  opacity: 1;
  transition: left 3s, opacity 1s, top  3s;
}


.navList span:nth-child(even), .navList span:nth-child(even) a {
  color: var(--mid-blue);
}

.navList span:nth-child(even):before {
  background-image: url(/holo-assistant/static/media/mid-heart.16a2ae42.svg);
}

.overlay {
  width: 100%;
  height: 100%;
  position: relative;
}

.frameOverlay {
  position: absolute;
  width: 104.5%;
  height: 104.5%;
  left: -2.25%;
  top: -2.25%;

}

.drawing {
  position: absolute;
  bottom: 0.35%;
  right: 0px;
  width: 75%;
}

.eyes {
  position: absolute;
  top: 6.25%;
  right: 36%;
  width: 11.25%;
  opacity: 1;
  -webkit-user-select: none;
          user-select: none;
}

@media all and (max-width: 1200px) {
  .navList span:before {
    background-image: none;
    font-size: 1em;
    width: 0.225em;
    color: var(--dark-blue);
  }

  .navList span:nth-child(even):before {
    background-image: none;
    color: var(--mid-blue);
  }

  main {
    width:  100%;
    margin: 7em 0 0 0;  
  }



}
